import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { BillingserviceService } from '@app/modules/opd/components/billing/billingservice.service';
import { PatientdataService } from '@app/modules/opd/components/allpatients/patientdata.service';
import { Params } from '@angular/router';
import { UtilService } from '@core/services/utils.service';
@Component({
  selector: 'app-billing',
  templateUrl: './billing.component.html',
  styleUrls: ['./billing.component.scss']
})
export class BillingComponent implements OnInit {
  public ordersCount;
  public patientData;
  public navLinks = {
    clinical: false,
    lab: false,
    imaging: false,
    payment: false
  };
  constructor(
    private router: Router,
    private billingService: BillingserviceService,
    private activatedRoute: ActivatedRoute,
    private patientdataService: PatientdataService,
    private utilService: UtilService
  ) {
    const currentRouter = router.url;
    const routerUrlArray = currentRouter && currentRouter.split('/');
    if (
      routerUrlArray.length &&
      this.navLinks.hasOwnProperty(routerUrlArray[routerUrlArray.length - 1])
    ) {
      this.getActiveLinks(routerUrlArray[routerUrlArray.length - 1]);
    }
  }

  ngOnInit() {
    this.activatedRoute.queryParams.subscribe((params: Params) => {});
    this.billingService.notifications$.subscribe(data => {
      this.ordersCount = data['imaging'] + data['lab'] + data['orders'];
    });
    this.billingService.activeNavLink$.subscribe(data => {
      this.getActiveLinks(data);
    });
    // getting patient data from the service
    this.patientData = this.patientdataService.getPatienDataForBilling();
    const age = this.patientData ? this.patientData.age : null;
    if (age || age === 0) {
      this.patientData.age = this.utilService.parseAgeIndays(age).get('patientAge');
    }
    if (this.patientData && this.patientData.birthDt) {
      this.patientData.age = this.utilService
        .parseDob(this.patientData.birthDt)
        .get('patientAge');
    }

    if (!this.patientData) {
      this.router.navigate(['../add-patient'], {
        relativeTo: this.activatedRoute
      });
    }
  }

  getActiveLinks(linkName) {
    this.navLinks = {
      clinical: false,
      lab: false,
      imaging: false,
      payment: false
    };
    this.navLinks[linkName] = true;
  }
  getAvatorImg(patientData) {
    const image =
      patientData && patientData.gender === 'Female'
        ? 'female-ico.png'
        : 'male-ico.png';
    return `https://s3.ap-south-1.amazonaws.com/dru.assets/images/app/icons/${image}`;
  }
  // navigate to the clinical billing screen
  clinicalBilling() {
    this.getActiveLinks('clinical');
    this.router.navigate(['../billing'], {
      relativeTo: this.activatedRoute
    });
  }
  // navigate to the lab billing screen
  labBilling() {
    this.getActiveLinks('lab');
    this.router.navigate(['../billing', 'lab'], {
      relativeTo: this.activatedRoute
    });
  }
  // navigate to the imaging billing screen
  imagingBilling() {
    this.getActiveLinks('imaging');
    this.router.navigate(['../billing', 'imaging'], {
      relativeTo: this.activatedRoute
    });
  }
  payment() {
    this.getActiveLinks('payment');
    this.router.navigate(['../billing', 'payment'], {
      relativeTo: this.activatedRoute
    });
  }
}
