import { Component, ViewEncapsulation, OnInit, Inject } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { OpdApiService, UserService } from '@core/services';
import { ToastrService } from 'ngx-toastr';
import { PatientdataService } from '@app/modules/opd/components/allpatients/patientdata.service';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { UserSession } from '@core/models/user-session';
@Component({
  encapsulation: ViewEncapsulation.None,
  selector: 'app-otpgeneration',
  templateUrl: './otpgeneration.component.html',
  styleUrls: ['./otpgeneration.component.scss']
})
export class OtpgenerationComponent implements OnInit {
  formGroup: FormGroup;
  patientDataForOtpGeneration: any;

  constructor(
    private formBuilder: FormBuilder,
    private opdService: OpdApiService,
    private patientdataService: PatientdataService,
    private toastr: ToastrService,
    public dialogRef: MatDialogRef<OtpgenerationComponent>,
    private userService: UserService,
    @Inject(MAT_DIALOG_DATA) public modelData
  ) {
    this.userSession = this.userService.getUser();
  }
  private userSession: UserSession;

  ngOnInit() {
    this.initForm();
  }
  initForm() {
    console.log('modal data', this.modelData);
    this.formGroup = this.formBuilder.group({
      otp: null
    });
  }
  otpVerification() {
    const patientId = this.patientdataService.getUserData();
    const payload = this.formGroup.value;
    payload.patientId = patientId;
    this.opdService.otpVerification(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          // if otp verified we need to path the patient data to the form
          this.dialogRef.close(response);
        } else if (response.responseCode === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  resendOtp() {
    this.patientDataForOtpGeneration = this.patientdataService.getPatienDataForBilling();
    const payload = {
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId,
      authenticatedUserId: this.userSession.regId,
      patientId: this.patientDataForOtpGeneration.patientId,
      isReGenerate: true
    };
    this.opdService.otpGeneration(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.toastr.info(
            'info',
            'OTP IS GENERATED AND SENT TO PATIENT REGISTERED MOBILE NUMBER'
          );
          this.patientdataService.setUserData(
            this.patientDataForOtpGeneration.patientId
          );
        } else if (response.responseCode === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      err => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  cancelModel() {
    this.dialogRef.close();
  }
}
