import { Injectable, Inject } from '@angular/core';
import { APP_CONSTANTS } from '@app/app.constant';
import { AppConstant } from '@core/models/app-constant';
@Injectable({
  providedIn: 'root'
})
export class OpdServiceService {

  constructor(@Inject(APP_CONSTANTS) private appConstants: AppConstant) {

   }


  getBucketsConfig() {
    return {
      accessKeyId: this.appConstants.buckets.profileBuckt.accessKeyId,
      secretAccessKey: this.appConstants.buckets.profileBuckt.secretAccessKey,
      region: this.appConstants.buckets.profileBuckt.region
    };
  }

  getBucketName() {
    return {
      bucketURL: this.appConstants.buckets.profileBuckt.BucketURL,
      url: this.appConstants.buckets.profileBuckt.url
    };
  }
}
