import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators, NgForm } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { BillingserviceService } from '@app/modules/opd/components/billing/billingservice.service';
import { DatePipe } from '@angular/common';
import { OpdApiService, RegisterationBillingService } from '@core/services';
import { AddreferencedoctorsComponent } from '@app/modules/opd/components/addreferencedoctors/addreferencedoctors.component';
import { MatDialog } from '@angular/material';
import { UserService, CLONEDEEP } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { PatientdataService } from '@app/modules/opd/components/allpatients/patientdata.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { MultipleApiResponse } from '@app/modules/opd/models/multiple-api-response-model';
import { OpdService } from './../../../services/opd.service';
@Component({
  selector: 'app-clinical',
  templateUrl: './clinical.component.html',
  styleUrls: ['./clinical.component.scss']
})
export class ClinicalComponent implements OnInit {
  formGroup: FormGroup;
  public isdisableservie = true;
  public isDisableAddtoList = true;
  public isDisableDoctor = true;
  public isDisableDept = false;
  public isEnableReson = false;
  public isDisablePaymentModes = true;
  private invoiceItemNo;
  public serviceList: any = [];
  public consultationList: any[] = [];
  public selectedConsultationList: any[] = [];
  public orgRefDoctorsList: any[] = [];
  public refOrgList = [];
  public clinicalServicesList: any = [];
  public departmentList: MultipleApiResponse;
  public deptWiseDoctorsList: any[] = [];
  public patientCategoriesList: MultipleApiResponse;
  public patientVisitDetails: any;
  public servicesUnitPrice: any;
  public ispayment = true;
  public lastObj: any;
  public visitTypesList: any[];
  public paymentApplicable: boolean;
  public patientData: any;
  public isEnableAddToList = false;
  public categorieData = {};
  public paymentModesList = [];
  public referenceList: any;
  private appDeptId: any;
  isDoctorSelected = null;
  @ViewChild('formDir')
  clinicalForm: NgForm;
  constructor(
    private formBuilder: FormBuilder,
    private router: Router,
    private OpdServicePrint: OpdService,
    private activatedRoute: ActivatedRoute,
    private opdService: OpdApiService,
    private registerationBillingSerivice: RegisterationBillingService,
    private toastr: ToastrService,
    private datePipe: DatePipe,
    private billingserviceService: BillingserviceService,
    private patientdataService: PatientdataService,
    public dialog: MatDialog,
    // private OpdServicePrint: OpdService,
    private userService: UserService,
    @Inject(CLONEDEEP) private cloner
  ) {
    this.userSession = this.userService.getUser();
  }

  private userSession: UserSession;
  ngOnInit() {
    this.initForm();
    this.fetchPaymentModes();
    // getting formdata from the service
    const clinicalFormData = this.billingserviceService.getconsultationFormData();
    this.consultationList = this.billingserviceService.getConsultations();
    this.multipleApiRespone();
    this.servicesList();
    // this.orgReferenceDoctors();
    // if (!clinicalFormData && !this.formGroup.get('patientCategoryNm').value) {
    //   this.router.navigate(['registration/add-patient']);
    // }
    if (clinicalFormData) {
      this.isDoctorSelected = clinicalFormData.isDoctorSelected ? clinicalFormData.isDoctorSelected : null;
      // this.isDoctorSelected = clinicalFormData.isDoctorSelected;
      this.formGroup.patchValue(
        this.billingserviceService.getconsultationFormData()
      );
      if (this.formGroup.get('selectedConsultationList').value) {
        this.selectedConsultationList = this.formGroup.get(
          'selectedConsultationList'
        ).value;
      }
      const deptId = this.formGroup.get('deptId').value;
      if (deptId) {
        this.deptwiseDoctors('clinical');
      }

      this.formGroup.get('refOrgNm').valueChanges.subscribe(val => {
        this.orgReferenceDoctors('organisationChange');
        this.formGroup.patchValue({
          refDoctorNm: clinicalFormData.refDoctorNm
        });
        this.onselctingconsultantNm();
      });
      this.formGroup.patchValue({
        referralType: clinicalFormData.referralType
      });

      this.formGroup.patchValue({
        refOrgNm: clinicalFormData.refOrgNm
      });

      this.formGroup.patchValue({ deptId: clinicalFormData.deptId });
      this.formGroup.patchValue({ doctorId: clinicalFormData.doctorId });

      this.ispayment = clinicalFormData.ispayment;
      this.isDisablePaymentModes = clinicalFormData.isDisablePaymentModes;

      this.formGroup.patchValue({
        visitTypeId: clinicalFormData.visitTypeId
      });

      if (clinicalFormData.visitTypeId) {
        this.isdisableservie = false;
      }
      this.patientData = this.patientdataService.getPatienDataForBilling();

      // Here we are updating the patient appointmentId comming from the appointment
      const appointmentData = this.patientdataService.getAppointmentData();
      if (appointmentData) {
        this.appDeptId = appointmentData.deptId;
        if (this.appDeptId) {
          this.deptwiseDoctors('appointment');
          this.isDisableDoctor = true;
          this.isDisableDept = true;
        }
        this.formGroup.patchValue({
          patientAppointmentId: appointmentData.patientAppointmentId,
          deptId: appointmentData.deptId,
          doctorId: {
            empId: appointmentData.doctorId,
            firstNm: appointmentData.doctorNm
          }
        });
        this.isDisablePaymentModes = false;
        this.isdisableservie = false;
        this.ispayment = true;
        this.formGroup.patchValue({
          paymentStatus: true
        });
        this.formGroup.patchValue({
          ispayment: this.ispayment
        });
        this.formGroup.patchValue({
          visitTypeId: 'NEW'
        });
      }
      if (this.patientData && this.patientData.isNew) {
        this.isDisablePaymentModes = false;
        this.isdisableservie = false;
        this.ispayment = true;

        this.formGroup.patchValue({
          paymentStatus: true
        });
        this.formGroup.patchValue({
          ispayment: this.ispayment
        });
        this.formGroup.patchValue({
          visitTypeId: 'NEW'
        });
      }
      this.formGroup.patchValue({
        patientCategoryName: clinicalFormData.patientCategoryNm
      });
    }
  }
  initForm() {
    this.formGroup = this.formBuilder.group({
      isDisablePaymentModes: null,
      dropDownList: null,
      refDoctorNm: null,
      refOrgNm: null,
      refDoctorContactNo: null,
      referralType: null,
      doctorId: [null, Validators.required],
      deptId: [null, Validators.required],
      patientCategoryNm: { patientCategoryName: 'GEN', patientCategoryId: 1 },
      patientCategoryId: null,
      visitTypeId: 'NEW',
      serviceName: null,
      quantity: [1, [Validators.minLength(1), Validators.maxLength(2)]],
      paymentStatus: null,
      visitedPatientCount: null,
      ispayment: true,
      paymentUncollectReason: [
        null,
        [
          Validators.required,
          Validators.pattern(/^[a-zA-Z ]*$/),
          Validators.minLength(2),
          Validators.maxLength(100)
        ]
      ],
      selectedConsultationList: null,
      patientAppointmentId: null,
      isDoctorSelected: null

    });

    // saving formdata into service
    this.formGroup.valueChanges.subscribe(() => {
      // this.formGroup.patchValue({ paymentStatus: this.ispayment });
      this.billingserviceService.setConsultationFormData(this.formGroup.value);
    });

    this.formGroup.get('referralType').valueChanges.subscribe(val => {
      this.orgReferenceDoctors('referralChange');
    });



  }
  servicesList() {
    const patientCategoryName = this.formGroup.get('patientCategoryNm').value;
    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      type: 'CLINICAL',
      patientCategoryName: patientCategoryName.patientCategoryName,
      currentDate: this.datePipe.transform(new Date(), 'yyyy-MM-dd')
    };
    this.opdService.servicesListForLabAndImaging(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.serviceList = response.data.clinicalServicesList;
          // this list is useful for printing message if no clinical services are there
          this.clinicalServicesList = response.data.clinicalServicesList;
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  changeDoctor() {
    this.formGroup.patchValue({ serviceName: null });
    this.isDisableAddtoList = true;
    this.isEnableAddToList = false;
    this.isDoctorSelected = true;
    this.formGroup.patchValue({isDoctorSelected: true});
  }
  changePatientcategory() {
    this.formGroup.patchValue({ serviceName: null });
    this.isDisableAddtoList = true;
    this.isEnableAddToList = false;
  }
  serviceUnitPrice() {
    this.isDisableAddtoList = true;
    this.isEnableAddToList = false;
    const { serviceId, billServiceType } = this.formGroup.get(
      'serviceName'
    ).value;
    if (serviceId) {
      const patientCategoryName = this.formGroup.get('patientCategoryNm').value;
      const doctorId = this.formGroup.get('doctorId').value;
      const payload = {
        orgId: this.userSession.orgId,
        authenticatedUserId: this.userSession.regId,
        orgGroupId: this.userSession.orgGroupId,
        billServiceType,
        serviceId,
        startDate: this.datePipe.transform(new Date(), 'yyyy-MM-dd'),
        doctorId: doctorId.empId,
        patientCategoryName: patientCategoryName.patientCategoryName
      };

      this.registerationBillingSerivice.serviceUnitPrice(payload).subscribe(
        (response: any) => {
          if (response.responseCode === 'E200' && response.data) {
            this.servicesUnitPrice = response.data;
            this.servicesUnitPrice.taxPercntg = this.servicesUnitPrice.taxPercentage;
            if (
              this.servicesUnitPrice.serviceAmount &&
              this.formGroup.get('quantity').value &&
              this.formGroup.get('serviceName').value
            ) {
              this.isDisableAddtoList = false;
              this.isEnableAddToList = true;
            }
          } else if (response.responseCode === 'E400') {
            this.toastr.error('Failure!', response.responseMessage);
          }
        },
        () => {
          this.toastr.error('Failure!', 'Something went wrong');
        }
      );
    }
  }
  refDoctorComparator(o1, o2) {
    return o1 && o2 && o1.refDoctorId === o2.refDoctorId;
  }
  refTypeComparator(o1, o2) {
    // && o1.referralType === o2.referralType
    return o1 && o2 && o1 === o2;
  }
  refOrgComparator(o1, o2) {
    // .refOrgNm === o2.refOrgNm
    return o1 && o2 && o1 === o2;
  }
  selectUnits() {
    this.isEnableAddToList = false;
    if (this.formGroup.get('quantity').value) {
      this.isEnableAddToList = true;
    }
  }
  addToList() {
    const service = this.formGroup.get('serviceName').value;
    this.invoiceItemNo = this.billingserviceService.getInvoiceItemNumber();
    this.invoiceItemNo = this.invoiceItemNo + 1;
    this.billingserviceService.setInvoiceItemNumber(this.invoiceItemNo);
    const serviceName = this.formGroup.get('serviceName').value;
    const quantity = this.formGroup.get('quantity').value;
    const services = this.servicesUnitPrice;
    const obj = { ...serviceName, ...services };
    obj.quantity = quantity;
    obj.invoiceItemNo = this.invoiceItemNo;
    this.serviceList = this.serviceList.filter(function(value) {
      return value.serviceId !== service.serviceId;
    });
    this.consultationList = this.cloner(this.consultationList);
    if (this.consultationList && this.consultationList.length) {
      const data = this.consultationList.find(item => item.serviceId === obj.serviceId);
      if (data) {
        this.toastr.warning('Already Added to List');
      } else {
      this.consultationList.push(obj);
      }
    } else {
      this.consultationList.push(obj);
    }
    this.selectedConsultationList.push(obj);
    this.formGroup.patchValue({
      selectedConsultationList: this.selectedConsultationList,
      quantity: 1
    });
    this.isDisableAddtoList = true;
    this.isEnableAddToList = false;
    this.formGroup.get('serviceName').reset();
  }

  addtoBilling() {
    this.addToList();
    if (this.consultationList.length) {
      this.billingserviceService.setConsultations(this.consultationList);
    } else {
      this.toastr.warning('Please Add Services');
    }
  }
  clearForm() {
    this.serviceList = this.serviceList.concat(this.consultationList);
    this.serviceList.sort((a, b) =>
      a.serviceName !== b.serviceName
        ? a.serviceName < b.serviceName
          ? -1
          : 1
        : 0
    );
    this.ispayment = false;
    this.isDisableAddtoList = true;
    this.isEnableAddToList = false;
    this.formGroup.reset();
    this.clinicalForm.resetForm();
    this.formGroup.patchValue({ quantity: 1 });
    this.consultationList = [];
    this.selectedConsultationList = [];
    this.billingserviceService.setConsultationFormData([]);
    this.billingserviceService.setConsultations([]);
  }
  orgReferenceDoctors(type) {
    if (type === 'referralChange') {
      this.orgRefDoctorsList = [];
      this.refOrgList = [];
    }
    this.formGroup.patchValue({
      refDoctorContactNo: null
    });
    if (type !== 'organisationChange') {
      this.formGroup.patchValue({
        refOrgNm: null
      });
    }
    this.formGroup.patchValue({
      refDoctorNm: null
    });

    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGroupId: this.userSession.orgGroupId,
      referralType: this.formGroup.get('referralType').value,
      refOrgNm: this.formGroup.get('refOrgNm').value
    };

    this.opdService.orgDoctors(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          if (type === 'organisationChange') {
            this.orgRefDoctorsList = [];
            this.orgRefDoctorsList = response.data;
            // this.orgRefDoctorsList = this.orgRefDoctorsList.filter(function(
            //   value
            // ) {
            //   return value.refDoctorNM;
            // });
          }
          if (type !== 'organisationChange') {
            this.orgRefDoctorsList = [];
            this.refOrgList = [];
            this.refOrgList = response.data;
            this.orgRefDoctorsList = response.data;
            this.refOrgList = this.refOrgList.filter(function(
              value
            ) {
              return value.refOrgNm;
            });
          }
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }

  deptwiseDoctors(type) {
   this. deptWiseDoctorsList = [];
 this.formGroup.get('doctorId').reset();
    this.formGroup.get('serviceName').reset();
    const deptId = this.formGroup.get('deptId').value;
    this.isDisableDoctor = true;
    if (deptId) {
      const payload = {
        orgId: this.userSession.orgId,
        authenticatedUserId: this.userSession.regId,
        orgGrpId: this.userSession.orgGroupId,
        designation: 'Doctor',
        deptId
      };
      if (type && type === 'appointment') {
        payload.deptId = this.appDeptId;
      }
      this.opdService.deptwiseDoctors(payload).subscribe(
        (response: any) => {
          if (response.responseCode === 'E200') {
            this.deptWiseDoctorsList = response.data;
            this.isDoctorSelected = false;
            if (this.billingserviceService.getconsultationFormData()) {
              this.isDoctorSelected = true;
            }
            this.formGroup.patchValue({isDoctorSelected: false});
            if (this.deptWiseDoctorsList && !this.deptWiseDoctorsList.length) {
              this.toastr.warning(
                'warning!',
                'Please select the department which contains doctors'
              );
            }
            if (this.deptWiseDoctorsList.length) {
              this.isDisableDoctor = false;
            }
          } else if (response.response === 'E400') {
            this.toastr.error('Failure!', response.responseMessage);
          }
        },
        () => {
          this.toastr.error('Failure!', 'Something went wrong');
        }
      );
    }
  }
  departmentComparator(o1, o2) {
    // o1.deptId === o2.deptId
    return o1 && o2 && o1 === o2;
  }
  doctorComparator(o1, o2) {
    return o1 && o2 && o1.firstNm === o2.firstNm;
  }
  patientCategoryComparator(o1, o2) {
    return (
      o1 &&
      o2 &&
      // (o1.patientCategoryNm === o2.patientCategoryNm ||
      o1.patientCategoryName === o2.patientCategoryName
    );
  }
  patientCategories() {
    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId
    };
    this.opdService.patientCategories(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.patientCategoriesList = response.data;
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  onselctingconsultantNm() {
    const refDoctorNm = this.formGroup.get('refDoctorNm').value;
    this.formGroup.patchValue({
      refDoctorContactNo: refDoctorNm.refDocContactNo
    });
  }
  addReferenceDoctors() {
    const dialogRef = this.dialog.open(AddreferencedoctorsComponent, {
      data: {},
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      // this.orgReferenceDoctors(obj);
    });
  }

  fetchVisitTypes() {
    this.opdService.fetchVisitTypes().subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.visitTypesList = response.data;
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }

  listOfPatientVisitDetails() {
    const patientCategoryNm = this.formGroup.get('patientCategoryNm').value;
    const deptId = this.formGroup.get('deptId').value;
    const patientData = this.patientdataService.getPatienDataForBilling();
    const doctor = this.formGroup.get('doctorId').value;
    if (!deptId || !doctor || !patientCategoryNm) {
      this.formGroup.patchValue({ visitTypeId: 2 });
      return;
    }
    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      feeValidDays: doctor.feeValidDays,
      feeValidVisits: doctor.feeValidVisits,
      doctorId: doctor.empId,
      patientId: patientData.patientId,
      visitTypeId: 2
    };
    this.opdService.listOfPatientVisitDetails(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.patientVisitDetails = response.data;
          this.isDisablePaymentModes = false;
          this.isdisableservie = false;
          const firstObj = this.patientVisitDetails[0];
          if (
            this.patientVisitDetails.length === 1 &&
            firstObj.noPreviousVisits
          ) {
            this.formGroup.patchValue({ visitTypeId: 'NEW' });
            this.ispayment = true;
            // this.isOriginalPayment=true;
            this.isdisableservie = false;
            this.formGroup.patchValue({
              paymentStatus: true
            });
            this.formGroup.patchValue({
              ispayment: this.ispayment
            });
            this.toastr.warning('Warning', 'No previous visits found');
          } else {
            this.lastObj = this.patientVisitDetails[
              this.patientVisitDetails.length - 1
            ];
            this.ispayment = this.lastObj.paymentApplicable;
            this.formGroup.patchValue({
              visitedPatientCount: this.lastObj.visitedPatientCount
            });
            this.formGroup.patchValue({
              paymentStatus: this.lastObj.paymentApplicable
            });
            this.formGroup.patchValue({
              ispayment: this.ispayment
            });
          }
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  createVisitId(form: NgForm) {
    if (this.isEnableReson) {
      if (form.invalid) {
        return;
      }
    }
    // const invalid = [];
    // const controls = this.formGroup.controls;
    // for (const name in controls) {
    //     if (controls[name].invalid) {
    //         invalid.push(name);
    //     }
    // }

    if (!this.isEnableReson) {
      let deptId, doctorId, category, visitTypeId;
      deptId = this.formGroup.get('deptId').value;
      doctorId = this.formGroup.get('doctorId').value;
      category = this.formGroup.get('patientCategoryNm').value;
      visitTypeId = this.formGroup.get('visitTypeId').value;

      if (!deptId || !doctorId || !category || !visitTypeId) {
        return;
      }
    }
    const payload = this.formGroup.value;
    const visitType = this.formGroup.get('visitTypeId').value;
    (payload['createdUsrId'] = this.userSession.regId),
      (payload['orgId'] = this.userSession.orgId),
      (payload['authenticatedUserId'] = this.userSession.regId),
      (payload['orgGrpId'] = this.userSession.orgGroupId),
      (payload['visitDt'] = this.datePipe.transform(new Date(), 'yyyy-MM-dd')),
      (payload['invoiceDate'] = this.datePipe.transform(
        new Date(),
        'yyyy-MM-dd'
      )),
      (payload['invoiceFrom'] = 'REG'),
      (payload['visitType'] = visitType);
    if (visitType === 'NEW') {
      payload['visitTypeId'] = 1;
    } else {
      payload['visitTypeId'] = 2;
    }
    const refDoctorData = this.formGroup.get('refDoctorNm').value;
    const patientCategory = this.formGroup.get('patientCategoryNm').value;
    const doctor = this.formGroup.get('doctorId').value;
    if (refDoctorData) {
      payload['refDoctorId'] = refDoctorData.refDoctorId;
      payload['refDoctorNm'] = refDoctorData.refDoctorNm;
      payload['refDocContactNo'] = refDoctorData.refDocContactNo;
    }
    if (doctor) {
      payload['doctorId'] = doctor.empId;
      payload['doctorNm'] = doctor.firstNm;
      payload['feeValidDays'] = doctor.feeValidDays;
      payload['feeValidVisits'] = doctor.feeValidVisits;
    }
    if (patientCategory) {
      // "patientCategoryNm":{"patientCategoryId":1,"patientCategoryName":"GEN","displayName":"General"}
      // payload['PatientCategoryNm'] = patientCategory.patientCategoryName;
      // payload['PatientCategoryId'] = patientCategory.patientCategoryId; payload['patientCategoryNm'] =
      payload['patientCategoryNm'] = patientCategory.patientCategoryName;
      payload['patientCategoryId'] = patientCategory.patientCategoryId;
    }
    const patientData = this.patientdataService.getPatienDataForBilling();
    if (patientData) {
      if (patientData.appointmentId) {
        payload['patientAppointmentId'] = patientData.appointmentId;
      }
      payload['hosPatientId'] = patientData.hosPatientId;
      payload['patientId'] = patientData.patientId;
      if (patientData.tempHosPatientId) {
        payload['tempHosPatientId'] = patientData.tempHosPatientId;
      }
    }
    payload['honorificNm'] = '';
    this.opdService.insertPatientBill(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          if (response !== null) {
            this.toastr.success('success!', response.responseMessage);
            if (
              response.data.registrationPrintType &&
              response.data.registrationPrintType === 'Case Sheet'
            ) {
              this.OpdServicePrint.generateCasesheet(response.data);
            } else {
              this.OpdServicePrint.generateToken(response.data);
            }
            this.billingserviceService.setInvoiceItemNumber(0);
            this.billingserviceService.setConsultations([]);
            this.ispayment = null;
            this.billingserviceService.setConsultationFormData([]);
            this.patientdataService.setUserData(null);
            this.patientdataService.setPatienDataForBilling(null);
            this.patientdataService.setAppointmentData(null);
            this.router.navigate(['registration/add-patient']);
          }
        } else if (response.responseCode === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  payment() {
    if (this.consultationList.length) {
      this.billingserviceService.setProceedToBillingActiveLink();
      this.billingserviceService.setConsultations(this.consultationList);
      this.router.navigate(['../payment'], {
        relativeTo: this.activatedRoute
      });
    } else {
      this.toastr.warning('Please Add Services');
    }
  }
  fetchPaymentModes() {
    const payload = {
      orgId: this.userSession.orgId,
      masterType: 'REFERRAL TYPE',
      authenticatedUserId: this.userSession.regId
    };

    this.opdService.fetchCommonValues(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.referenceList = response.data;
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', 'Something went wrong');
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }

  iaPaymentApplicable(value) {
    const patientCategoryNm = this.formGroup.get('patientCategoryNm').value;
    const deptId = this.formGroup.get('deptId').value;
    const doctorId = this.formGroup.get('doctorId').value;
    const visitTypeId = this.formGroup.get('visitTypeId').value;
    this.isDisablePaymentModes = false;
    this.formGroup.patchValue({
      isDisablePaymentModes: this.isDisablePaymentModes
    });
    if (!deptId || !doctorId || !patientCategoryNm) {
      this.formGroup.patchValue({ visitTypeId: 1 });
      return;
    }
    if (patientCategoryNm && deptId && doctorId.empId && visitTypeId) {
      this.isdisableservie = false;
    }
    this.ispayment = value;
    this.formGroup.get('paymentUncollectReason').reset();
    this.formGroup.patchValue({ paymentStatus: this.ispayment });
  }
  deleteSelectedServices(obj) {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: { title: 'Are You Sure Want To Delete ?' },
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.serviceList.push(obj);
        this.serviceList.sort((a, b) =>
          a.serviceName !== b.serviceName
            ? a.serviceName < b.serviceName
              ? -1
              : 1
            : 0
        );
        this.consultationList = this.consultationList.filter(function(value) {
          return value.serviceId !== obj.serviceId;
        });
        this.billingserviceService.setConsultations(this.consultationList);
      }
    });
  }
  cancelForm() {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: { title: 'Are You Sure Want To Cancel ?' },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.billingserviceService.setConsultationFormData([]);
        this.router.navigate(['registration/add-patient']);
      }
    });
  }
  /**
   * @function { * } onChangeMode : function to onchange payment mode
   * @param {boolean} : payment true/false
   */
  onChangeMode(paymentMode) {
    if (!paymentMode) {
      this.formGroup.patchValue({serviceName: null});
      this.formGroup.patchValue({quantity: 1});
       this.isEnableAddToList = false;
      const paymentStatus = this.formGroup.get('paymentStatus').value;
      if (paymentStatus) {
        this.isEnableReson = true;
      }
    }
    this.ispayment = paymentMode;
    this.serviceList = [...this.serviceList, ...this.consultationList];
    // this.serviceList.push(this.consultationList);
    this.consultationList = [];
    this.billingserviceService.setConsultations([]);
    this.formGroup.patchValue({
      paymentStatus: paymentMode
    });
    if (paymentMode) {
      this.formGroup.get('paymentUncollectReason').reset();
      this.isEnableReson = false;
    } else {
    }
  }

  multipleApiRespone() {
    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      orgGroupId: this.userSession.orgGroupId
    };
    this.opdService
      .multipleApiRespone<MultipleApiResponse>(payload)
      .subscribe(responseList => {
        this.patientCategoriesList = responseList[0].data;
        this.departmentList = responseList[1].data;
      });
  }
  get isWhomToMeetInvalid() {
    // return (
    //   this.formGroup.get('deptId').invalid ||
    //   this.formGroup.get('doctorId').invalid ||
    //   this.formGroup.get('patientCategoryNm').invalid
    // );
    if (
      !this.formGroup.get('deptId').value ||
      !this.formGroup.get('doctorId').value ||
      !this.formGroup.get('patientCategoryNm').value
    ) {
      return false;
    }
    return true;
  }
}
