import { Component, OnInit , ViewChild} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Validators , NgForm} from '@angular/forms';
import { OpdApiService, UtilService, UserService } from '@core/services';
import { ToastrService } from 'ngx-toastr';
import { UserSession } from '@core/models/user-session';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { OpdService } from '@app/modules/opd/services/opd.service';
@Component({
  selector: 'app-addreferencedoctors',
  templateUrl: './addreferencedoctors.component.html',
  styleUrls: ['./addreferencedoctors.component.scss']
})
export class AddreferencedoctorsComponent implements OnInit {
  formGroup: FormGroup;
  private refDoctorId: number;
  public referenceDocList: any[] = [];
  public isAdd = true;
  public paymentModesList = [];
  public referenceList: any;
  honorificsList;
  @ViewChild('formDir')
  registrationForm: NgForm;

  constructor(
    public dialogRef: MatDialogRef<AddreferencedoctorsComponent>,
    private formBuilder: FormBuilder,
    private toastr: ToastrService,
    private opdApiService: OpdApiService,
    private userService: UserService,
    public dialog: MatDialog,
    private opdService: OpdApiService,
  ) {
    this.userSession = this.userService.getUser();
  }
  private userSession: UserSession;
  commonValues = <any>{};

  ngOnInit() {
    this.initForm();
      this.fetchCommonValues('HONORIFICS');
    this.fetchReferenceDoctor();
    this.fetchReferenceTypes();


  }
  initForm() {

    this.formGroup = this.formBuilder.group({
      referralType: [
        null,
        [
          Validators.required,
        ]
      ],
      honorificNm: [
        null
      ],
      refDoctorNm: [
        null,
        [
          // Validators.required,
          Validators.pattern(/^[a-zA-Z ]*$/),
          Validators.minLength(3),
          Validators.maxLength(20)
        ]
      ],
      refOrgNm: [
        null,
        [
          // Validators.required,
          Validators.pattern(/^[a-zA-Z ]*$/),
          Validators.minLength(3),
          Validators.maxLength(20)
        ]
      ],
      refDocContactNo: [
        null,
        [
          Validators.pattern(/(5|6|7|8|9)\d{9}$/),
          Validators.minLength(10),
          Validators.maxLength(10),
          // Validators.required
        ]
      ],


    });
  }
  checkIsDoctor() {
    this.formGroup.patchValue({refDoctorNm: null});
    this.formGroup.patchValue({honorificNm: null});
    this.formGroup.patchValue({refDocContactNo: null});
    this.formGroup.patchValue({refOrgNm: null});
    this.formGroup.get('refDoctorNm').clearValidators();
    this.formGroup.get('refDoctorNm').updateValueAndValidity();
    this.formGroup.get('honorificNm').clearValidators();
    this.formGroup.get('honorificNm').updateValueAndValidity();
    if (this.formGroup.get('referralType').value &&
     this.formGroup.get('referralType').value === 'Doctor') {
      this.formGroup.get('refDoctorNm').setValidators(
        [Validators.required,
        Validators.pattern(/^[a-zA-Z ]*$/),
      Validators.minLength(3),
      Validators.maxLength(20)]);
      this.formGroup.get('refDoctorNm').updateValueAndValidity();
      this.formGroup.get('honorificNm').setValidators(
        [Validators.required]);
      this.formGroup.get('honorificNm').updateValueAndValidity();
    }
  }
  fetchCommonValues(masterType) {
    const payload = {
      masterType
    };
    this.opdApiService.fetchCommonValues(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200' && response.data) {
        this.honorificsList = response.data.filter(data => {
        return data.attributeCode !== 'b/o.';
          });
          this.honorificsList.map(data => {
           if (data.description === 'Doctor') {
            data.description = 'Dr.';
           }

          });
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  fetchReferenceTypes() {
    const payload = {
      orgId: this.userSession.orgId,
      masterType: 'REFERRAL TYPE',
      authenticatedUserId: this.userSession.regId
    };

    this.opdService.fetchCommonValues(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.referenceList = response.data;
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', 'Something went wrong');
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  addReferenceDoctor(formData, actionType) {
    const payload = formData;
    (payload.orgId = this.userSession.orgId),
      (payload.orgGrpId = this.userSession.orgGroupId);
    payload.createdUsrId = this.userSession.regId;
    if (this.refDoctorId) {
      payload.refDoctorId = this.refDoctorId;
    }
    this.opdApiService.addReferenceDoctor(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          if (this.referenceDocList.length) {
          this.referenceDocList = this.referenceDocList.filter(
            item => item.refDoctorId !== formData.refDoctorId
          );
        }
          this.fetchReferenceDoctor();
          // this.referenceDocList.push(response.data);
          this.refDoctorId = null;
          if (actionType === 'update') {
            this.isAdd = true;
          }
          if (actionType === 'close') {
            this.dialogRef.close();
          } else {
            this.registrationForm.resetForm();
          }
          this.clearFormData();
        } else if (response.responseCode === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.warning('Failure!', 'something went wrong');
      }
    );
  }
  editForm(doctorData) {
    this.formGroup.patchValue(doctorData);
    this.refDoctorId = doctorData.refDoctorId;
    this.isAdd = false;
  }
  fetchReferenceDoctor() {
    const payload = {
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId,
      authenticatedUserId: this.userSession.regId
    };
    this.opdApiService.fetchReferenceDoctor(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200' && response.data) {
          this.referenceDocList = response.data;
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  deleteReferenceDoctor(doctor) {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: { title: 'Are You Sure Want To Delete ?' },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
    const payload = {
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId,
      authenticatedUserId: this.userSession.regId,
      refDoctorId: doctor.refDoctorId
    };
    this.opdApiService.deleteReferenceDoctor(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.referenceDocList = this.referenceDocList.filter(
            x => x.refDoctorId !== doctor.refDoctorId
          );
          this.toastr.success('success!', response.responseMessage);
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
});
  }
  clearFormData() {
    this.registrationForm.resetForm();
    this.formGroup.get('refDoctorNm').clearValidators();
    this.formGroup.get('refDoctorNm').updateValueAndValidity();
    this.formGroup.get('honorificNm').clearValidators();
    this.formGroup.get('honorificNm').updateValueAndValidity();
  }
  closeForm() {
    this.dialogRef.close();
  }
}
