import { NgModule } from '@angular/core';
import { Routes, RouterModule, Router } from '@angular/router';
import { OpdComponent } from './opd.component';
import { AllpatientsComponent } from '../opd/components/allpatients/allpatients.component';
import { AddpatientComponent } from '../opd/components/addpatient/addpatient.component';
import { SearchPatientComponent } from '@app/modules/opd/components/search-patient/search-patient.component';
import { BillingComponent } from '@app/modules/opd/components/billing/billing.component';
import { ClinicalComponent } from '@app/modules/opd/components/billing/clinical/clinical.component';
import { ImagingComponent } from '@app/modules/opd/components/billing/imaging/imaging.component';
import { LabComponent } from '@app/modules/opd/components/billing/lab/lab.component';
import { PaymentComponent } from '@app/modules/opd/components/billing/payment/payment.component';
import { PageNotFoundComponentComponent } from './page-not-found-component/page-not-found-component.component';

export const OpdRoutes: Routes = [
  {
    path: '',
    component: OpdComponent,
    children: [
      {
        path: 'all-patients',
        component: AllpatientsComponent
      },
      {
        path: 'add-patient',
        component: AddpatientComponent
      },
      {
        path: 'search-patient',
        component: SearchPatientComponent
      },
      {
        path: 'billing',
        component: BillingComponent,
        children: [
          {
            path: 'clinical',
            component: ClinicalComponent
          },
          {
            path: 'imaging',
            component: ImagingComponent
          },
          {
            path: 'lab',
            component: LabComponent
          },
          {
            path: 'payment',
            component: PaymentComponent
          },
          {
            path: '',
            redirectTo: 'clinical',
            pathMatch: 'prefix'
          }
        ]
      },
      { path: '**', component: PageNotFoundComponentComponent }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(OpdRoutes)],
  exports: [RouterModule]
})
export class OpdRoutingModule {}
export const routingComponents = [
  AllpatientsComponent,
  AddpatientComponent,
  SearchPatientComponent,
  BillingComponent,
  ClinicalComponent,
  ImagingComponent,
  LabComponent,
  PaymentComponent,
  PageNotFoundComponentComponent
];
