  private userSession: UserSession;

  private getCasesheetData(data) {

    const printData = <CaseSheetTokenPrint>{
      consultantDoc: '',
      generatedBy: ''
    };

    // consultantDoc
    printData.consultantDoc = data.doctorNm || '';
    // visit number

    // printData.visitNo = data.patientVisitId || '';

    printData.visitNo =
      data.visitType === 'NEW' ? '1 - NEW' : data.visitCount + ' - FOLLOW UP';

    // payment applicable
    printData.paymentApplicable = data.paymentStatus ? 'Yes' : 'No';
    // referred by
    printData.referredBy = data.refDoctorNm || '';
    // dept name
    printData.deptName = data.deptNm || '';
    // patient name
    printData.patientName = (data.honorificNm ? (data.honorificNm + ' ') : '') + data.patientNm || '';
    // patientid
    printData.patientId = data.hosPatientId || '';
    // mobile number
    printData.mobile = data.mobileNo || '';
    // genderprint-changes__common-reg-fix
    printData.gender = data.gender || '';
    // token no
    printData.tokenNo = data.tokenNo || '';
    // date of birth
    if (data.birthDt) {
      const ageMap = this.utilService.parseDob(data.birthDt);
      // printData.age = ageFormatter(ageMap);
      printData.age = String(ageMap.get('patientAge'));
    } else if (data.age) {
      const ageMap = this.utilService.parseAgeIndays(data.age);
      printData.age = String(ageMap.get('patientAge'));
    }
    // pin code
    printData.pincode = data.pincode || '';
    // country
    printData.country = data.countrynm || '';
    // address one
    printData.addressOne = data.addressline1 || '';
    // country
    printData.city = data.citynm || '';
    // patientAddress
    printData.patientAddress = data.addressLine1;

    // valid upto
    printData.validUpto =
      (data.feeValidDt &&
        data.feeValidVisits &&
        this.datePipe.transform(data.feeValidDt, 'dd-MM-yyyy') +
          ' / ' +
          data.feeValidVisits +
          ' visits which ever is earlier') ||
      'NA';

    // generated by
    printData.generatedBy = this.userSession.userName;
    // hospital name
    if (this.userSession.orgList) {
      const currentOrg = this.userSession.orgList.find(org => {
        return org.orgId === this.userSession.orgId;
      });
      const orgName = currentOrg && currentOrg.orgName;
      printData.companyName = orgName;
    }

    if (data.visitDt) {
      printData.date = this.datePipe.transform(
        data.visitDt,
        'dd-MM-yyyy - hh: mm a'
      );
    } else {
      printData.date = this.datePipe.transform(
        new Date(),
        'dd-MM-yyyy - hh: mm a'
      );
    }

    return printData;
  }

  generateToken(data) {
    const caseSheetData = this.getCasesheetData(data);
    let printSettings;
    const printSettingsObject = this.printService.getPrintSettingsObject();
    if (printSettingsObject && Object.keys(printSettingsObject).length) {
      printSettings = this.cloner(printSettingsObject.token);
    } else {
      printSettings = this.cloner(this.defSettings.token);
    }
    this.printService.printToken(caseSheetData, printSettings);
  }

  async generateCasesheet(data) {
    let printSettings;
    const printSettingsObject = this.printService.getPrintSettingsObject();

    if (printSettingsObject && Object.keys(printSettingsObject).length) {
      printSettings = this.cloner(printSettingsObject.casesheet);
    } else {
      printSettings = this.cloner(this.defSettings.casesheet);
    }
    // const printSettings = this.userSession.printsettingsObj
    //   ? this.cloner(this.userSession.printsettingsObj.casesheet)
    //   : this.cloner(this.defSettings.casesheet);git

    const caseSheetData = this.getCasesheetData(data) as any;
    const companyAddress = ['', ''];
    const {getCurrentOrgName } = this.utilService;

    // company address line 1
    if (this.userSession.orgInfo.addressOne) {
      companyAddress[0] =  (`${this.userSession.orgInfo.addressOne}, `);
    }

    if (this.userSession.orgInfo.city) {
      companyAddress[0] += (`${this.userSession.orgInfo.city}`);
    }

    if (this.userSession.orgInfo.state) {
      companyAddress[1] = (`${this.userSession.orgInfo.state}, `);
    }
    if (this.userSession.orgInfo.country) {
      companyAddress[1] += (this.userSession.orgInfo.country);
    }

    caseSheetData.header = {
      companyName: (getCurrentOrgName(this.userSession)),
      companyAddress
    };

    await this.printService.downloadHeaderAndFooter(printSettings);

    const profileDataUri = await this.utilService.convertToDataURLviaCanvas(
      this.userSession.imagePath + '?q=' + Date.now()
    );

    caseSheetData.profileLogo = profileDataUri;

    if (printSettings.patientImageNeeded && data.imagePath) {
      const patientImageDataUri = await this.utilService.convertToDataURLviaCanvas(
        data.imagePath + '?' + new Date().getTime()
      );
      caseSheetData.patientImage = patientImageDataUri;
    }

    const type1 = [49, 93]; // emr optho case-sheet
    const type2 = [454, 137, 139, 527, 126, 127, 1164]; // highsight eye hospital

    // emr case optho casesheet
    if (type1.includes(this.userSession.orgId)) {
      const eyeImage = await this.utilService.convertToDataURLviaCanvas(
        `https://s3.ap-south-1.amazonaws.com/dru.assets/images/app/Eye-Diagrams.jpg?q=${Date.now()}`
      );
      caseSheetData.eyeDiagram = eyeImage;
      this.printService.getOpthoPrint(caseSheetData);
    } else if (type2.includes(this.userSession.orgId)) {
      // highsight eye hospital
      const headerBannerImage = await this.utilService.convertToDataURLviaCanvas(
        'https://s3.ap-south-1.amazonaws.com/dru.assets/images/app/HS_BNR.jpg?' +
          Date.now()
      );
      caseSheetData.headerBanner = headerBannerImage;
      this.printService.getNewOptometristPrint(caseSheetData);
    } else {
      this.printService.printCaseSheeet(caseSheetData, printSettings);
    }
  }

