import { Component, OnInit, OnDestroy } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { UtilService } from '@core/services/utils.service';
import { ActivatedRoute, Router, NavigationExtras } from '@angular/router';
import { Params } from '@angular/router';
import { Subscription } from 'rxjs';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import { OpdApiService, UserService } from '@core/services';
import { PatientdataService } from '@app/modules/opd/components/allpatients/patientdata.service';
import { UserSession } from '@core/models/user-session';
import { OpdService } from '../../services/opd.service';

@AutoUnsubscribe()
@Component({
  selector: 'app-search-patient',
  templateUrl: './search-patient.component.html',
  styleUrls: ['./search-patient.component.scss']
})
export class SearchPatientComponent implements OnInit, OnDestroy {
  searchType: string;
  patientData: string;
  screen: string;
  isFetchingData = false;
  navigateToSearchPatient = false;
  public patientType: string;
  queryParamSubscription: Subscription;
  public editable = false;
  constructor(
    private opdApiService: OpdApiService,
    private toastr: ToastrService,
    private utilService: UtilService,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private patientdataService: PatientdataService,
    private userService: UserService,
    private opdService: OpdService
  ) {
    this.userSession = this.userService.getUser();
  }
  private userSession: UserSession;
  patientId: string;
  name: string;
  aadhaar: string;
  mobile: string;
  patientList = [];
  FingerPrintData = [];

  ngOnInit() {
    this.queryParamSubscription = this.activatedRoute.queryParams.subscribe(
      (params: Params) => {
        this.patientData = params['patientData'];
        this.searchType = params['searchType'];
        // this code for get the subscribed finger print data.
        this.opdService.fingerData.subscribe(res => {
          if (res) {
            this.FingerPrintData = res;
          } else {
            this.FingerPrintData = [];
          }
        });
        this.patientSearch(this.patientData, this.searchType);
    });
  }

/**
 * @description this method is used for search the patint based on data.
 * @function patientSearch
 * @param searchType
 * @param patientData
 */
    patientSearch(patientData, searchType) {
    this.patientdataService.setUserData(null);
    this.patientList = [];
    this.isFetchingData = true;
    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      searchType: this.searchType,
      patientData: this.patientData,
    };
    if (this.FingerPrintData.length) {
      payload['fingerType'] = this.FingerPrintData[0].fingerType;
      payload['fingerprint'] = this.FingerPrintData[0].fingerprint;
      payload['leftRightIndicator'] = this.FingerPrintData[0].leftRightIndicator;
    }
    this.opdApiService.patientSearchDetailsWithFingerPrint(payload).subscribe(
      (response: any) => {
        this.isFetchingData = false;
        if (response.responseCode === 'E200') {
          this.patientList = response.data.patientList;
          this.patientType = response.data.patientType;
          if (response.data && response.data.patientList.length) {
            this.patientList = response.data.patientList.map(patient => {
              patient.mobileOrAadhaarNo = this.patientData;
              if (patient.age || patient.age === 0) {
                patient.ageYears = this.utilService
                  .parseAgeIndays(patient.age)
                  .get('patientAge');
              }
              if (patient.birthDt) {
                patient.ageYears = this.utilService
                  .parseDob(patient.birthDt)
                  .get('patientAge');
              }
              return patient;
            });
          } else {
            this.isFetchingData = false;
            // const navigationExtras: NavigationExtras = {};  , navigationExtras
            // this.router.navigate(['/registration']);
            this.patientData = '';
          }
          this.FingerPrintData = [];
          this.opdService.fingerData.next(null);
        } else if (response.response === 'E400') {
          // const navigationExtras: NavigationExtras = {};
          // this.router.navigate(['/search-patient'], { replaceUrl: true });
          this.toastr.error('Failure!', response.responseMessage);
        }
        this.FingerPrintData = [];
        this.opdService.fingerData.next(null);
      },
      () => {
        this.isFetchingData = false;
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  // navigate to the clinical billing screen
  clinicalBilling(patientData) {
    if (patientData.patientAdmitId) {
      this.patientdataService.setPatienDataForBilling(patientData);
      this.toastr.warning(
        'success!',
        'Ipd patient not allowed for consultation'
      );
      return;
    }
    if (!patientData.patientAdmitId) {
      this.patientdataService.setPatienDataForBilling(patientData);
      this.router.navigate(['../billing'], {
        relativeTo: this.activatedRoute
      });
    }
  }
  // navigate to the lab billing screen
  labBilling(patientData) {
    this.patientdataService.setPatienDataForBilling(patientData);
    this.router.navigate(['../billing', 'lab'], {
      relativeTo: this.activatedRoute
    });
  }
  // navigate to the imaging billing screen
  imagingBilling(patientData) {
    this.patientdataService.setPatienDataForBilling(patientData);
    this.router.navigate(['../billing', 'imaging'], {
      relativeTo: this.activatedRoute
    });
  }
  editPatientDetails(patientData) {
    this.editable = true;
    this.opdService.fingerPrintApiData.next(patientData.fingerPrints);
    this.opdService.editable.next(this.editable);
    this.navigateToSearchPatient = true;
    this.patientdataService.setUserData(patientData.patientId);
    this.patientdataService.setNavigateToSearchPatient(
      this.navigateToSearchPatient
    );
    this.router.navigate(['../add-patient'], {
      relativeTo: this.activatedRoute
    });
  }
  getAvatorImg(patientData) {
    const image =
      patientData && patientData.gender === 'Female'
        ? 'female-ico.png'
        : 'male-ico.png';
    return `https://s3.ap-south-1.amazonaws.com/dru.assets/images/app/icons/${image}`;
  }
  registerPatient(patientData) {
    if (patientData && !patientData.hosPatientId && !patientData.patientOrgId) {
      this.patientdataService.setPatienDataForBilling(patientData);
      const navigationExtras: NavigationExtras = {
        queryParams: {
          screen: 'otherOrgPatient'
        }
      };
      this.router.navigate(['/registration/add-patient'], navigationExtras);
    }
    if (patientData && patientData.patientOrgId) {
      this.patientdataService.setUserData(patientData.patientId);
      const navigationExtras: NavigationExtras = {
        queryParams: {
          screen: 'withinOrg'
        }
      };
      this.router.navigate(['/registration/add-patient'], navigationExtras);
    }
    if (patientData && patientData.appointmentDt) {
      this.patientdataService.setUserData(patientData.patientId);
      const navigationExtras: NavigationExtras = {
        queryParams: {
          appointment: 'appointment'
        }
      };
      this.router.navigate(['/registration/add-patient'], navigationExtras);
    }
  }

  ngOnDestroy() {}
}
