import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class BillingserviceService {
  constructor() {}
  invoiceItemNo = 0;
  consultationFormData: any;
  labFormData: any;
  imagingFormData: any;
  emrTests: any;
  emrImagingTests: any;
  orders = {
    imagingTest: [],
    labTests: [],
    consultations: []
  };
  lab = {
    tests: [],
    panels: [],
    packages: []
  };
  imaging = {
    tests: [],
    panels: [],
    packages: []
  };

  count = 0;
  private notifications = new Subject();
  notifications$ = this.notifications.asObservable();
  private activeNavLink = new Subject();
  activeNavLink$ = this.activeNavLink.asObservable();

  setProceedToBillingActiveLink() {
    this.activeNavLink.next('payment');
  }
  getConsultations() {
    return this.orders.consultations;
  }
  setConsultations(data: any) {
    this.orders.consultations = data;
    this.notifications.next(this.processNotifications());
  }
  // lab relates data
  getLabTests() {
    return this.lab.tests;
  }
  getLabPanels() {
    return this.lab.panels;
  }
  getLabPackages() {
    return this.lab.packages;
  }

  setLabTests(data: any) {
    this.lab.tests = data;
    this.notifications.next(this.processNotifications());
  }
  setLabPanels(data: any) {
    this.lab.panels = data;
    this.notifications.next(this.processNotifications());
  }
  setLabPackages(data: any) {
    this.lab.packages = data;
    this.notifications.next(this.processNotifications());
  }

  setImageTests(data: any) {
    this.imaging.tests = data;
    this.notifications.next(this.processNotifications());
  }
  setImagePanels(data: any) {
    this.imaging.panels = data;
    this.notifications.next(this.processNotifications());
  }
  setImagePackages(data: any) {
    this.imaging.packages = data;
    this.notifications.next(this.processNotifications());
  }

  getImageTests() {
    return this.imaging.tests;
  }
  getImagePanels() {
    return this.imaging.panels;
  }
  getImagePackages() {
    return this.imaging.packages;
  }

  getEmrTests() {
    return this.emrTests;
  }
  setEmrTests(tests) {
    this.emrTests = tests;
  }
  getEmrImagingTests() {
    return this.emrImagingTests;
  }
  setEmrImagingTests(tests) {
    this.emrImagingTests = tests;
  }
  processNotifications() {
    const counts = {
      orders: 0,
      imaging: 0,
      lab: 0
    };
    counts.orders = this.orders.consultations.length
      ? this.orders.consultations.length
      : 0;
    counts.lab = [...this.lab.packages, ...this.lab.panels, ...this.lab.tests]
      .length
      ? [...this.lab.packages, ...this.lab.panels, ...this.lab.tests].length
      : 0;
    counts.imaging = [
      ...this.imaging.tests,
      ...this.imaging.panels,
      ...this.imaging.packages
    ].length
      ? [
          ...this.imaging.tests,
          ...this.imaging.panels,
          ...this.imaging.packages
        ].length
      : 0;
    return counts;
  }
  getconsultationFormData() {
    return this.consultationFormData;
  }
  setConsultationFormData(data) {
    this.consultationFormData = data;
  }
  getLabFormData() {
    return this.labFormData;
  }
  setLabFormData(data) {
    this.labFormData = data;
  }
  getImageFormdata() {
    return this.imagingFormData;
  }
  setImageFormdata(data) {
    this.imagingFormData = data;
  }
  getInvoiceItemNumber() {
    return this.invoiceItemNo;
  }
  setInvoiceItemNumber(data) {
    this.invoiceItemNo = data;
  }
}
