import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Validators } from '@angular/forms';

@Component({
  selector: 'app-conform-model',
  templateUrl: './conform-model.component.html',
  styleUrls: ['./conform-model.component.scss']
})
export class ConformModelComponent implements OnInit {
  public isContinue = false;
  public formGroup: FormGroup;

  constructor(
    public dialogRef: MatDialogRef<ConformModelComponent>,
    private formBuilder: FormBuilder
  ) {}
  ngOnInit() {
    this.initForm();
  }
  initForm() {
    this.formGroup = this.formBuilder.group({
      mobileNo: [
        null,
        [
          Validators.required,
          Validators.pattern(/(5|6|7|8|9)\d{9}$/),
          Validators.minLength(10),
          Validators.maxLength(10)
        ]
      ],
      aadhaarNo: [
        null,
        [
          Validators.pattern('[0-9]*'),
          Validators.minLength(12),
          Validators.maxLength(12)
        ]
      ]
    });
  }
  skipModel() {
    this.dialogRef.close('skipped');
  }
  continue() {
    this.isContinue = true;
  }
  getMobileOrAadhaarNo() {
    this.dialogRef.close(this.formGroup.value);
  }
  cancel() {
    this.dialogRef.close();
  }
}
