import { Component, OnInit } from '@angular/core';
import { DatePipe } from '@angular/common';
import { ToastrService } from 'ngx-toastr';
import { MatDatepicker } from '@angular/material/datepicker';
import { PatientdataService } from '@app/modules/opd/components/allpatients/patientdata.service';
import { Router, NavigationExtras, ActivatedRoute } from '@angular/router';
import { OpdApiService, UtilService } from '@core/services';
import { UserService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { MatDialog } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { OpdService } from '@app/modules/opd/services/opd.service';
import { FormControl, AbstractControl } from '@angular/forms';
import { BillingserviceService } from '../billing/billingservice.service';
import { AddreferencedoctorsComponent } from '../addreferencedoctors/addreferencedoctors.component';

@Component({
  selector: 'app-allpatients',
  templateUrl: './allpatients.component.html',
  styleUrls: ['./allpatients.component.scss']
})
export class AllpatientsComponent implements OnInit {
  allPatientList = [];
  patientDemographicData: any;
  isShowFilter = false;
  show = false;
  searchType = '';
  searchText: '';
  public totalCount: number;
  public editable = false;
  public navigateToAllPatients = false;
  doctorSearch: '';
  referral: '';
  inputName: '';
  inputDate: '';
  fromDate: '';
  toDate: '';
  departmentList: any;
  doctorsList: any;
  referenceDocList: any;
  department: any;
  inputCategory: any;
  inputBookingStatus: any;
  inputPatientStatus: any;
  maxDate = new Date();
  filterControl: AbstractControl;
  constructor(
    private opdService: OpdApiService,
    private toastr: ToastrService,
    private datePipe: DatePipe,
    private utilService: UtilService,
    private patientdataService: PatientdataService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private dialog: MatDialog,
    private OpdServicePrint: OpdService,
    private userService: UserService,
    private billingserviceService: BillingserviceService
  ) {
    this.userSession = this.userService.getUser();
  }
  private userSession: UserSession;

  ngOnInit() {
    this.billingserviceService.setConsultationFormData(null);
    this.allPatients(
      this.datePipe.transform(new Date(), 'yyyy-MM-dd'),
      this.datePipe.transform(new Date(), 'yyyy-MM-dd'),
      null
    );
    this.departments();
    this.doctorsData();
    this.fetchReferenceDoctor();
    this.filterControl = new FormControl('dayWise');
    this.filterControl.valueChanges.subscribe(value => {
      this.searchType = value;
      this.allPatients(this.fromDate, this.toDate, this.searchType);
    });
  }
  showDatepicker() {
    this.show = !this.show;
  }

  addReferenceDoctors() {
    const dialogRef = this.dialog.open(AddreferencedoctorsComponent, {
      data: {},
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      // this.orgReferenceDoctors(obj);
    });
  }

  allPatients(fromDate, toDate, value) {
    const payload = {
      fromDate,
      toDate,
      searchType: this.searchType,
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId
    };
    this.opdService.fetchAllPatients(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.allPatientList = response.data;
          this.totalCount = this.allPatientList.length;
          this.allPatientList = response.data.map(patient => {
            if (patient.age || patient.age === 0) {
              patient.patientAge = this.utilService
                .parseAgeIndays(patient.age)
                .get('patientAge');
            }
            if (patient.birthDt) {
              patient.patientAge = this.utilService
                .parseDob(patient.birthDt)
                .get('patientAge');
            }
            return patient;
          });
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      err => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  deletePatientVisitDetails(patientVisitId) {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: { title: 'Are You Sure Want To Delete ?' },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const payload = {
          patientVisitId,
          orgId: this.userSession.orgId,
          authenticatedUserId: this.userSession.regId,
          orgGrpId: this.userSession.orgGroupId
        };
        this.opdService.deletePatientVisitDetails(payload).subscribe(
          (response: any) => {
            if (response.responseCode === 'E200') {
              this.allPatientList = this.allPatientList.filter(
                x => x.patientVisitId !== payload.patientVisitId
              );
              this.toastr.success(response.responseMessage);
            } else if (response.response === 'E400') {
              this.toastr.error('Failure!', response.responseMessage);
            }
          },
          _ => {
            this.toastr.error('Failure!', 'Something went wrong');
          }
        );
      }
    });
  }
  showFilter() {
    this.isShowFilter = !this.isShowFilter;
  }
  onChangeDates() {
    if (!this.fromDate || !this.toDate) {
      return false;
    }
    this.allPatients(this.fromDate, this.toDate, null);
  }
  // navigate to the clinical billing screen
  clinicalBilling(patientData) {
    if (patientData.patientAdmitId) {
      patientData.visitType = null;
      this.patientdataService.setPatienDataForBilling(patientData);
      this.toastr.warning(
        'success!',
        'Ipd patient not allowed for consultation'
      );
      return;
    }
    if (!patientData.patientAdmitId) {
      patientData.visitType = null;
      patientData.feeValidDt = null;
      patientData.feeValidVisits = null;
      patientData.patientCategoryNm = null;
      patientData.patientStatus = null;
      patientData.patientType = null;
      patientData.patientVisitId = null;
      patientData.registrationPrintType = null;
      patientData.tokenNo = null;
      patientData.deptNm = null;
      patientData.doctorNm = null;
      patientData.paymentStatus = null;
      patientData.isNew = null;
      this.billingserviceService.setConsultationFormData({ patientCategoryName: 'GEN' });
    this.patientdataService.setPatienDataForBilling(patientData);
      this.router.navigate(['../billing'], {
        relativeTo: this.activatedRoute
      });
    }
  }
  editPatientData(patient) {
    // hiding search options
    this.OpdServicePrint.fingerPrintApiData.next(patient.fingerPrints);
    this.editable = true;
    this.OpdServicePrint.editable.next(this.editable);
    this.navigateToAllPatients = true;
    this.patientdataService.setNavigateToAllPatients(this.navigateToAllPatients);
    this.patientdataService.setUserData(patient.patientId);
    this.patientdataService.setUserVisitIdData(patient.patientVisitId);
    const navigationExtras: NavigationExtras = {
      queryParams: {
        allPatientScreen: 'allPatientScreen'
      }
    };
    this.router.navigate(['/registration/add-patient'], navigationExtras);
  }
  departments() {
    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId
    };
    this.opdService.departments(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.departmentList = response.data;
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.data.responseMessage);
        }
      },
      err => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  doctorsData() {
    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      designation: 'Doctor'
    };

    this.opdService.getEmpListBasedOnDesignation(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.doctorsList = response.data;
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.data.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }

  fetchReferenceDoctor() {
    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId
    };
    this.opdService.fetchReferenceDoctor(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200' && response.data) {
          this.referenceDocList = response.data;
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }

  getSelectedText() { }
  getAvatorImg(patientData) {
    const image =
      patientData && patientData.gender === 'Female'
        ? 'female-ico.png'
        : 'male-ico.png';
    return `https://s3.ap-south-1.amazonaws.com/dru.assets/images/app/icons/${image}`;
  }

  generateToken(patient) {
    if (
      patient.registrationPrintType &&
      patient.registrationPrintType === 'Case Sheet'
    ) {
      this.OpdServicePrint.generateCasesheet(patient);
    }
    if (
      patient.registrationPrintType &&
      patient.registrationPrintType !== 'Case Sheet'
    ) {
      this.OpdServicePrint.generateToken(patient);
    }
  }
}
