import { Component, OnInit, Inject } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { BillingserviceService } from '@app/modules/opd/components/billing/billingservice.service';
import { MatDatepicker } from '@angular/material/datepicker';
import { DatePipe } from '@angular/common';
import {
  OpdApiService,
  LabApiService,
  RegisterationBillingService,
  UserService,
  CLONEDEEP
} from '@core/services';
import { UserSession } from '@core/models/user-session';
import { Router, ActivatedRoute } from '@angular/router';
import { MatDialog } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { PatientdataService } from '@app/modules/opd/components/allpatients/patientdata.service';
import { Subscription } from 'rxjs';
@Component({
  selector: 'app-imaging',
  templateUrl: './imaging.component.html',
  styleUrls: ['./imaging.component.scss']
})
export class ImagingComponent implements OnInit {
  public subPanelsAndTests: any;
  private selctedValue = 'tests';
  private allServices: any;
  public imagingTests: any[] = [];
  public imagingPanels: any[] = [];
  public imagingPackages: any[] = [];
  public selectedServicesList: any[];
  public selectedServicesListForData: any[];
  public orgRefDoctorsList: any[];
  public refOrgList = [];
  private servicesUnitPrice: any;
  public patientCategoriesList: any[] = [];
  formGroup: FormGroup;
  public referenceList: any;
  public isTest = false;
  public isPanel = false;
  public isPackage = false;
  public isDisableAddtoList = true;
  public disableService = true;
  private patientData;
  public emrPatientTests;
  public emrImagingTests;
  public emrImagingpanels;
  amountDefinedEmrPatientTests: any;
  emrPatientTestsDuplicate: any;
  dataForCompare: any[];
  private apiSubscription: Subscription;
  private labFormDataForEmrTests = null;
  constructor(
    private formBuilder: FormBuilder,
    private opdService: OpdApiService,
    private registerationBilling: RegisterationBillingService,
    private toastr: ToastrService,
    private datePipe: DatePipe,
    private billingserviceService: BillingserviceService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    public dialog: MatDialog,
    private userService: UserService,
    private patientdataService: PatientdataService,
    @Inject(CLONEDEEP) private cloner
  ) {
    this.userSession = this.userService.getUser();
  }
  private userSession: UserSession;

  ngOnInit() {
    this.initForm();
    this.servicesListForLabAndImaging();
    this.imagingTests = this.billingserviceService.getImageTests();
    this.imagingPanels = this.billingserviceService.getImagePanels();
    this.imagingPackages = this.billingserviceService.getImagePackages();
    this.fetchPaymentModes();
    // this.orgReferenceDoctors();
    this.patientCategories();
    const clinicalFormData = this.billingserviceService.getconsultationFormData();
    const labFormData = this.billingserviceService.getLabFormData();
    this.labFormDataForEmrTests = labFormData;
    // getting formdata from the service
    const imagingFormData = this.billingserviceService.getImageFormdata();
    this.patientData = this.patientdataService.getPatienDataForBilling();
    // if (!imagingFormData) {
    //   this.router.navigate(['registration/add-patient']);
    // }
    if (imagingFormData) {
      this.formGroup.patchValue(this.billingserviceService.getImageFormdata());
      if (clinicalFormData && clinicalFormData.patientCategoryNm) {
        this.formGroup.patchValue({
          patientCategoryName: clinicalFormData.patientCategoryNm
        });
      }
      if (!clinicalFormData && !clinicalFormData.patientCategoryNm) {
        this.formGroup.patchValue({
          patientCategoryName: imagingFormData.patientCategoryNm
        });
      }
      this.formGroup.patchValue({
        refDoctorNM: imagingFormData.refDoctorNM
      });
    }
    if (!this.billingserviceService.getEmrImagingTests()) {
      this.fetchEmrPatients();
    }
  }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  initForm() {
    this.formGroup = this.formBuilder.group({
      refDoctorNm: null,
      refOrgNm: null,
      refDoctorContactNo: null,
      referralType: null,
      date: null,
      serviceName: null,
      priority: 'Medium',
      value: null,
      patientVisitId: null,
      dateOfOrder: this.datePipe.transform(new Date(), 'yyyy-MM-dd'),
      patientCategoryNm: { patientCategoryName: 'GEN', patientCategoryId: 1 },
      patientCategoryId: null,
      testPlanDttm: this.datePipe.transform(new Date(), 'yyyy-MM-dd'),
      emrImagingTests: null,
      emrImagingpanels: null,
      emrSubpanels: null
    });
    // saving formdata into service
    this.formGroup.valueChanges.subscribe(() => {
      this.billingserviceService.setImageFormdata(this.formGroup.value);
    });
  }
  servicesListForLabAndImaging() {
    const patientCategoryName = this.formGroup.get('patientCategoryNm').value;
    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      type: 'IMAGING',
      patientCategoryName: patientCategoryName.patientCategoryName,
      currentDate: this.datePipe.transform(new Date(), 'yyyy-MM-dd')
    };
    this.opdService.servicesListForLabAndImaging(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.allServices = response.data;
          this.selectedServices(this.selctedValue);
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  selectedServices(selectedValue) {
    this.formGroup.get('serviceName').reset();
    this.isDisableAddtoList = true;
    if (selectedValue === 'tests') {
      this.isTest = true;
      this.isPanel = false;
      this.isPackage = false;
      this.selectedServicesList = this.allServices.testList;
      this.selectedServicesListForData = this.allServices.testList;
    } else if (selectedValue === 'panels') {
      this.isTest = false;
      this.isPanel = true;
      this.isPackage = false;
      this.selectedServicesList = this.allServices.panelList;
      this.selectedServicesListForData = this.allServices.panelList;
    } else {
      this.isTest = false;
      this.isPanel = false;
      this.isPackage = true;
      this.selectedServicesList = this.allServices.packageList;
      this.selectedServicesListForData = this.allServices.packageList;
    }
  }
  fetchPaymentModes() {
    const payload = {
      orgId: this.userSession.orgId,
      masterType: 'REFERRAL TYPE',
      authenticatedUserId: this.userSession.regId
    };

    this.opdService.fetchCommonValues(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.referenceList = response.data;
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', 'Something went wrong');
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  fetchEmrPatients() {
    const payload = {
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      orgId: this.userSession.orgId,
      hospitalPatientId: this.patientData.hosPatientId,
      patientId: this.patientData.patientId,
      type: 'Imaging'
    };
    this.opdService.fetchEmrTestsForPatient(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.emrPatientTestsDuplicate = null;
          this.billingserviceService.setEmrImagingTests(null);
          this.emrPatientTests = response.data;
          if (this.labFormDataForEmrTests
            &&
            this.labFormDataForEmrTests.patientVisitId
            &&
             this.emrPatientTests.emrvisitLists) {
            // here we are filtering the visit id from the list which selected in lab .
            // If isit selected from the lab wee are showing all the  visits from the emr
            this.emrPatientTests.emrvisitLists = this.emrPatientTests.emrvisitLists.filter(data => {
              return data.patientVisitId === this.labFormDataForEmrTests.patientVisitId;
            });
          }
        } else if (response.responseCode === 'E400') {
          // this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  changePatientcategory() {
    this.formGroup.patchValue({ serviceName: null });
    this.disableService = true;
    this.isDisableAddtoList = true;
  }
  serviceUnitPrice() {
    this.isDisableAddtoList = true;
    this.disableService = true;
    const { serviceId, billServiceType } = this.formGroup.get(
      'serviceName'
    ).value;
    if (serviceId) {
      const patientCategoryName = this.formGroup.get('patientCategoryNm').value;
      const payload = {
        orgId: this.userSession.orgId,
        authenticatedUserId: this.userSession.regId,
        orgGroupId: this.userSession.orgGroupId,
        billServiceType,
        serviceId,
        startDate: this.datePipe.transform(new Date(), 'yyyy-MM-dd'),
        patientCategoryName: patientCategoryName.patientCategoryName,
        type: 'IMAGING'
      };

      this.registerationBilling.serviceUnitPrice(payload).subscribe(
        (response: any) => {
          if (response.responseCode === 'E200') {
            this.servicesUnitPrice = response.data;
            this.disableService = false;
            if (
              this.formGroup.get('testPlanDttm').value &&
              !this.disableService
            ) {
              this.isDisableAddtoList = false;
            }
          } else if (response.responseCode === 'E400') {
            this.toastr.error('Failure!', response.responseMessage);
          }
        },
        () => {
          this.toastr.error('Failure!', 'Something went wrong');
        }
      );
    }
  }
  // addToList() {
  //   const obj = {};
  //   obj['serviceName'] = this.formGroup.get('serviceName').value;
  //   obj['priority'] = this.formGroup.get('priority').value;
  //   obj['service'] = this.servicesUnitPrice;
  //   if (this.isTest) {
  //     this.imagingTests.push(obj);
  //     this.billingserviceService.setImageTests(this.imagingTests);
  //   } else if (this.isPanel) {
  //     this.imagingPanels.push(obj);
  //     this.billingserviceService.setImagePanels(this.imagingPanels);
  //   } else {
  //     this.imagingPackages.push(obj);
  //     this.billingserviceService.setImagePackages(this.imagingPackages);
  //   }
  //   // [this.labTests, this.labPanels, this.labPackages]
  //   // this.billingserviceService.setLabTests(this.labTests);
  // }
  disableAddToListButton() {
    // this.isDisableAddtoList = false;
    if (this.formGroup.get('testPlanDttm').value) {
      this.isDisableAddtoList = false;
    }
  }
  addToList() {
    const service = this.formGroup.get('serviceName').value;
    const serviceName = this.formGroup.get('serviceName').value;
    const priority = this.formGroup.get('priority').value;
    const testPlanDttm = this.formGroup.get('testPlanDttm').value;
    const services = this.servicesUnitPrice;
    services.testPlanDttm = testPlanDttm;
    const obj = { ...serviceName, ...services };
    obj.quantity = 1;
    const invoiceItemNo = this.billingserviceService.getInvoiceItemNumber();
    this.billingserviceService.setInvoiceItemNumber(invoiceItemNo + 1);
    obj.invoiceItemNo = invoiceItemNo + 1;
    obj.priority = priority;
    if (obj.type === 'PACKAGE' && obj.panelList && obj.panelList.length) {
      obj.panelList.forEach(element => {
        if (element.type === 'PANEL') {
          element.orgId = this.userSession.orgId;
          this.opdService.fetchSubpanelsAndTests(element).subscribe(
            (response: any) => {
              if (response.responseCode === 'E200') {
                this.subPanelsAndTests = response.data;
                if (this.subPanelsAndTests.subPanelList) {
                  element.subPanelsList = this.subPanelsAndTests.subPanelList;
                }
              } else if (response.responseCode === 'E400') {
                this.toastr.error('Failure!', response.responseMessage);
              }
            },
            () => {
              this.toastr.error('Failure!', 'Something went wrong');
            }
          );
        }
      });
    }
    if (obj.type === 'PANEL') {
      this.opdService.fetchSubpanelsAndTests(obj).subscribe(
        (response: any) => {
          if (response.responseCode === 'E200') {
            this.subPanelsAndTests = response.data;
            if (this.subPanelsAndTests.subPanelList) {
              obj.subPanelsList = this.subPanelsAndTests.subPanelList;
            }
            this.isDisableAddtoList = true;
          } else if (response.responseCode === 'E400') {
            this.toastr.error('Failure!', response.responseMessage);
          }
        },
        () => {
          this.toastr.error('Failure!', 'Something went wrong');
        }
      );
    }
    // Here  we are removing the selected services from test list
    if (this.isTest) {
      this.selectedServicesList = this.selectedServicesList.filter(function(
        value
      ) {
        return value.serviceId !== service.serviceId;
      });
      // this.allServices.testList = this.selectedServicesList;
      this.imagingTests = this.cloner(this.imagingTests);
        const data = this.imagingTests.find(item => item.serviceId === obj.serviceId);
        if (data) {
          this.toastr.warning('Already Added To List');
        } else {
          this.imagingTests.push(obj);
        }
    } else if (this.isPanel) {
      // Here  we are removing the selected services from panel list
      this.selectedServicesList = this.selectedServicesList.filter(function(
        value
      ) {
        return value.serviceId !== service.serviceId;
      });
      this.imagingPanels = this.cloner(this.imagingPanels);
      const data = this.imagingPanels.find(item => item.serviceId === obj.serviceId);
        if (data) {
          this.toastr.warning('Already Added To List');
        } else {
          this.imagingPanels.push(obj);
        }

      // this.formGroup.reset();
    } else {
      // Here  we are removing the selected services from package list
      this.selectedServicesList = this.selectedServicesList.filter(function(
        value
      ) {
        return value.serviceId !== service.serviceId;
      });
      this.imagingPackages = this.cloner(this.imagingPackages);
      const data = this.imagingPackages.find(item => item.serviceId === obj.serviceId);
        if (data) {
          this.toastr.warning('Already Added To List');
        } else {
          this.imagingPackages.push(obj);
        }

    }
    this.formGroup.get('serviceName').reset();
    this.formGroup.patchValue({ priority: 'Medium' });
    this.isDisableAddtoList = true;
    this.disableService = true;
  }

  addToViewOrderDetails(data) {
    if (this.apiSubscription) {
      this.apiSubscription.unsubscribe();
    }
    this.formGroup.patchValue({ patientVisitId: data.patientVisitId });
    const patientCategoryName = this.formGroup.get('patientCategoryNm').value;
    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId,
      type: 'EMR',
      patientCategoryNm: patientCategoryName.patientCategoryName,
      currentDt: this.datePipe.transform(new Date(), 'yyyy-MM-dd'),
      // currentDt: '2018-12-15',
      patientVisitId: data.patientVisitId
    };
    this.apiSubscription =
    this.opdService.servicesListForLabAndImaging(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.amountDefinedEmrPatientTests = response.data;
          this.emrImagingTests = this.amountDefinedEmrPatientTests.testList;
          this.emrImagingpanels = this.amountDefinedEmrPatientTests.panelList;
          // here we are filtering the imaging tests
          if (this.emrImagingTests) {
            this.emrImagingTests = this.emrImagingTests.filter(testsData => {
              return  testsData.billServiceType === 'Imaging';
            });
          }
          this.imagingTests = this.imagingTests.concat(
           this.emrImagingTests
          );
          // here we are filtering the imaging panels

          if (this.emrImagingpanels) {
            this.emrImagingpanels = this.emrImagingpanels.filter(testsData => {
              return testsData.billServiceType === 'Imaging' ;
            });
          }
          this.formGroup.patchValue({ emrImagingTests: this.emrImagingTests });
          this.formGroup.patchValue({
            emrImagingpanels: this.emrImagingpanels
          });
          this.imagingTests.map(tests => {
            const amountObj = tests.billServiceAmountObj;
            (tests.cgstTaxPercentage = amountObj.cgstTaxPercntg),
              (tests.serviceAmount = amountObj.serviceAmt),
              (tests.sgstTaxPercentage = amountObj.sgstTaxPercntg),
              (tests.taxCategoryCode = amountObj.taxCategoryCode),
              (tests.taxId = amountObj.taxId),
              (tests.taxPercentage = amountObj.taxPercntg),
              (tests.quantity = 1),
              (tests.isEmrTest = true);
            return tests;
          });
          this.billingserviceService.setImageTests(this.imagingTests);
          this.imagingPanels = this.imagingPanels.concat(
            this.emrImagingpanels
          );
          this.billingserviceService.setImagePanels(this.imagingPanels);
          this.imagingPanels.map(panels => {
            const amountObj = panels.billServiceAmountObj;
            (panels.cgstTaxPercentage = amountObj.cgstTaxPercntg),
              (panels.serviceAmount = amountObj.serviceAmt),
              (panels.sgstTaxPercentage = amountObj.sgstTaxPercntg),
              (panels.taxCategoryCode = amountObj.taxCategoryCode),
              (panels.taxId = amountObj.taxId),
              (panels.taxPercentage = amountObj.taxPercntg),
              (panels.quantity = 1),
              (panels.isEmrTest = true);
            this.opdService.fetchSubpanelsAndTests(panels).subscribe(
              (responseData: any) => {
                if (responseData.responseCode === 'E200') {
                  this.subPanelsAndTests = responseData.data;
                  if (this.subPanelsAndTests.subPanelList) {
                    panels.subPanelsList = this.subPanelsAndTests.subPanelList;
                    this.formGroup.patchValue({emrSubpanels: this.subPanelsAndTests.subPanelList });


                  }
                } else if (response.responseCode === 'E400') {
                  this.toastr.error('Failure!', response.responseMessage);
                }
              },
              () => {
                this.toastr.error('Failure!', 'Something went wrong');
              }
            );
            return panels;
          });
          this.emrPatientTestsDuplicate = this.emrPatientTests;
          this.billingserviceService.setEmrImagingTests(this.emrPatientTestsDuplicate);
          this.emrPatientTests = [];
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }

  addtoBilling() {
    this.addToList();
    if (
      this.imagingTests.length ||
      this.imagingPanels.length ||
      this.imagingPackages.length
    ) {
      this.billingserviceService.setImageTests(this.imagingTests);
      this.billingserviceService.setImagePanels(this.imagingPanels);
      this.billingserviceService.setImagePackages(this.imagingPackages);
    } else {
      this.toastr.warning('Please Add Services');
    }
  }
  clearForm() {
    this.isDisableAddtoList = true;
    this.disableService = true;
    this.selectedServicesList = this.selectedServicesList.concat(
      this.imagingTests
    );
    this.selectedServicesList = this.selectedServicesList.concat(
      this.imagingPanels
    );
    this.selectedServicesList = this.selectedServicesList.concat(
      this.imagingPackages
    );
    this.selectedServicesList.sort((a, b) =>
      a.serviceName !== b.serviceName
        ? a.serviceName < b.serviceName
          ? -1
          : 1
        : 0
    );
    this.formGroup.reset();
    this.imagingTests = [];
    this.imagingPanels = [];
    this.imagingPackages = [];
    this.billingserviceService.setImageFormdata([]);
    this.billingserviceService.setImageTests([]);
    this.billingserviceService.setImagePanels([]);
    this.billingserviceService.setImagePackages([]);
  }
  orgReferenceDoctors(type) {
    this.formGroup.patchValue({
      refDoctorContactNo: null
    });
    if (type !== 'organisationChange') {
      this.refOrgList = [];
      this.formGroup.patchValue({
        refOrgNm: null
      });
    }
    this.formGroup.patchValue({
      refDoctorNm: null
    });

    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGroupId: this.userSession.orgGroupId,
      referralType: this.formGroup.get('referralType').value,
      refOrgNm: this.formGroup.get('refOrgNm').value
    };

    this.opdService.orgDoctors(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          if (type === 'organisationChange') {
            this.orgRefDoctorsList = [];
            this.orgRefDoctorsList = response.data;
            // this.orgRefDoctorsList = this.orgRefDoctorsList.filter(function(
            //   value
            // ) {
            //   return value.refDoctorNM;
            // });
          }
          if (type !== 'organisationChange') {
            this.orgRefDoctorsList = [];
            this.refOrgList = [];
            this.refOrgList = response.data;
            this.orgRefDoctorsList = response.data;
            this.refOrgList = this.refOrgList.filter(function(value) {
              return value.refOrgNm;
            });
          }
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  patientCategories() {
    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGrpId: this.userSession.orgGroupId
    };
    this.opdService.patientCategories(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.patientCategoriesList = response.data;
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  refDoctorComparator(o1, o2) {
    return o1 && o2 && o1.refDoctorId === o2.refDoctorId;
  }
  refTypeComparator(o1, o2) {
    return o1 && o2 && o1.referralType === o2.referralType;
  }
  refOrgComparator(o1, o2) {
    return o1 && o2 && o1.refOrgNm === o2.refOrgNm;
  }
  patientCategoryComparator(o1, o2) {
    return (
      o1 &&
      o2 &&
      // (o1.patientCategoryNm === o2.patientCategoryNm ||
      o1.patientCategoryName === o2.patientCategoryName // )
    );
  }

  onselctingconsultantNm() {
    const refDoctorNm = this.formGroup.get('refDoctorNm').value;
    this.formGroup.patchValue({
      refDoctorContactNo: refDoctorNm.refDocContactNo
    });
  }
  payment() {
    if (
      this.imagingTests.length ||
      this.imagingPanels.length ||
      this.imagingPackages.length
    ) {
      this.billingserviceService.setProceedToBillingActiveLink();
      this.billingserviceService.setImageTests(this.imagingTests);
      this.billingserviceService.setImagePanels(this.imagingPanels);
      this.billingserviceService.setImagePackages(this.imagingPackages);
      this.router.navigate(['../payment'], {
        relativeTo: this.activatedRoute
      });
    } else {
      this.toastr.warning('Please Add Services');
    }
  }
  deleteSelectedServices(obj, type) {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: { title: 'Are You Sure Want To Delete ?' },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        if (type === 'tests') {
          this.selectedServicesList.push(obj);
          this.imagingTests = this.imagingTests.filter(function(value) {
            return value.serviceId !== obj.serviceId;
          });
          this.billingserviceService.setImageTests(this.imagingTests);
        }
        if (type === 'panels') {
          this.selectedServicesList.push(obj);
          this.imagingPanels = this.imagingPanels.filter(function(value) {
            return value.serviceId !== obj.serviceId;
          });
          this.billingserviceService.setImagePanels(this.imagingPanels);
        }
        if (!obj.isEmrTest) {
          this.selectedServicesList.push(obj);
        }
        if (obj.isEmrTest) {
          this.dataForCompare = [];
          this.dataForCompare = this.imagingTests.filter(function(value) {
            return value.isEmrTest && value.isEmrTest === true;
          });
          let emrPanels = this.imagingPanels.filter(function(value) {
            return value.isEmrTest && value.isEmrTest === true;
          });
          this.dataForCompare = this.dataForCompare.concat(emrPanels);
          emrPanels = [];
        }
        if (this.dataForCompare && !this.dataForCompare.length) {
          this.emrPatientTests = this.emrPatientTestsDuplicate;
          this.formGroup.patchValue({ patientVisitId: null });
        }
        if (type === 'packages') {
          this.selectedServicesList.push(obj);
          this.imagingPackages = this.imagingPackages.filter(function(value) {
            return value.serviceId !== obj.serviceId;
          });
          this.billingserviceService.setImagePackages(this.imagingPackages);
        }
        this.selectedServicesList.sort((a, b) =>
          a.serviceName !== b.serviceName
            ? a.serviceName < b.serviceName
              ? -1
              : 1
            : 0
        );
      }
    });
  }
  cancelForm() {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: { title: 'Are You Sure Want To Cancel ?' },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.billingserviceService.setImageTests([]);
        this.billingserviceService.setImagePanels([]);
        this.billingserviceService.setImagePackages([]);
        this.billingserviceService.setImageFormdata([]);
        this.router.navigate(['registration/add-patient']);
      }
    });
  }
}
