import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class PatientdataService {
  constructor() { }
  editable: any;
  appointmentData: any;
  patientDemographicData: string;
  patientVisitId: any;
  patientDataForBilling: any;
  navigateToSearchPatient: any;
  navigateToAllPatients: any;

  setUserData(data: any) {
    this.patientDemographicData = data;
  }
  setUserVisitIdData(visitId: any) {
  this.patientVisitId = visitId;
  }
  getUserVisitIdData() {
    return this.patientVisitId;
  }
  getUserData() {
    return this.patientDemographicData;
  }
  setAppointmentData(data: any) {
    this.appointmentData = data;
  }
  getAppointmentData() {
    return this.appointmentData;
  }
  setPatienDataForBilling(data: any) {
    this.patientDataForBilling = data;
  }
  getPatienDataForBilling() {
    return this.patientDataForBilling;
  }
  setEditable(data: any) {
    this.editable = data;
  }
  getEditable() {
    return this.editable;
  }
  setNavigateToSearchPatient(data: any) {
    this.navigateToSearchPatient = data;
  }
  getNavigateToSearchPatient() {
    return this.navigateToSearchPatient;
  }
  setNavigateToAllPatients(data: any) {
    this.navigateToAllPatients = data;
  }
  getNavigateToAllPatients() {
    return this.navigateToAllPatients;
  }
}
