import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';

@Pipe({
  name: 'searchfilter'
})
export class SearchfilterPipe implements PipeTransform {
  patientNm: any;
  private datePipe: DatePipe;
  // transform(
  //   allPatientList: any,
  //   searchText?: string,
  //   doctorSearch: string
  // ): any {
  //   if (searchText == null) {
  //     return allPatientList;
  //   }
  //   return allPatientList.filter(function(patient) {
  //     return (
  //       patient.deptNm.toLowerCase().indexOf(searchText.toLowerCase()) > -1
  //     );
  //   });
  // }

  transform(
    allPatientList: any,
    searchText: string,
    doctorSearch: string,
    inputName: string,
    inputDate: string,
    inputCategory: string,
    inputBookingStatus: string,
    inputPatientStatus: string,
    referral: string
  ) {
    if (allPatientList && allPatientList.length) {
      return allPatientList.filter(patient => {
        if (
          (searchText && !patient.deptNm) ||
          (doctorSearch && !patient.deptNm) ||
          (referral && !patient.refDoctorNm)
        ) {
          return false;
        }
        if (patient.deptNm) {
          if (
            searchText &&
            patient.deptNm.toLowerCase().indexOf(searchText.toLowerCase()) ===
              -1
          ) {
            return false;
          }
        }
        if (patient.doctorNm) {
          if (
            doctorSearch &&
            patient.doctorNm
              .toLowerCase()
              .indexOf(doctorSearch.toLowerCase()) === -1
          ) {
            return false;
          }
        }

        // referral type
        if (patient.refDoctorNm) {

          if (
            referral &&
            patient.refDoctorNm
              .toLowerCase()
              .indexOf(referral.toLowerCase()) === -1
          ) {
            return false;
          }
        }
        // patient category
        if (patient.patientCategoryNm) {
          if (
            inputCategory &&
            patient.patientCategoryNm
              .toLowerCase()
              .indexOf(inputCategory.toLowerCase()) === -1
          ) {
            return false;
          }
        }
        // patient status
        if (patient.patientStatus) {
          if (
            inputPatientStatus &&
            patient.patientStatus
              .toLowerCase()
              .indexOf(inputPatientStatus.toLowerCase()) === -1
          ) {
            return false;
          }
        }
        // patient name
        if (patient.patientNm) {
          if (
            inputName &&
            patient.patientNm.toLowerCase().indexOf(inputName.toLowerCase()) ===
              -1
          ) {
            return false;
          }
        }

        // patient visitDate
        // if (patient.visitDt) {
        //   const datePipe = new DatePipe('en-US');
        //   patient.visitDt = datePipe.transform(patient.visitDt, 'yyyy/MM/dd');
        //   if ( inputDate && patient.visitDt.toLowerCase().indexOf(inputDate.toLowerCase()) === -1) {
        //     return false;
        //   }
        // }
        // if (inputDate &&  (inputDate.length === 10 || inputDate.length === 2 || inputDate.length === 3 ||
        //   inputDate.length === 5 || inputDate.length === 6 ) ) {
        //   const splitedArray = inputDate.split('/') || [];
        //   if (splitedArray) {
        //     const reversArray = splitedArray.reverse();
        //     if (reversArray && patient.visitDt.indexOf(reversArray.join('/')) === -1) {
        //       return false;
        //     }
        //   }
        // }
        return true;
      });
    } else {
        return allPatientList;
    }
  }
}
