import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, NavigationExtras, Router, Params } from '@angular/router';
import { FormControl, AbstractControl } from '@angular/forms';
import { BillingserviceService } from '@app/modules/opd/components/billing/billingservice.service';
import { OpdService } from './services/opd.service';
import { Subscription } from 'aws-sdk/clients/workdocs';
import { PatientdataService } from './components/allpatients/patientdata.service';
import { MatDialog } from '@angular/material/dialog';
import { FingerPrintComponent } from '@shared/components/finger-print/finger-print.component';
@Component({
  selector: 'app-opd',
  templateUrl: './opd.component.html',
  styleUrls: ['./opd.component.scss']
})
export class OpdComponent implements OnInit {
  public allPatients;
  isEnable = false;
  patientId = '';
  name = '';
  aadhaar = '';
  mobile = '';
  searchType = '';
  patientData: any;
  inputValue: string;
  public placeholderValue: string;
  public editable = false;
  patinetSearchControl: AbstractControl;
  public pattern = '[0-9]*';
  public i = 0;
  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private billingserviceService: BillingserviceService,
    private opdService: OpdService,
    private patientDataService: PatientdataService,
    private dialog: MatDialog
  ) {

  }

  ngOnInit() {
    this.opdService.editable.subscribe(
      (data: any) => {
       this.editable = data;
      }
    );
    this.activatedRoute.queryParams.subscribe(
      (params: Params) => {
        this.allPatients = params['allPatientScreen'];

        if (!this.allPatients) {
          this.editable = false;
        }
      }
    );
    this.patinetSearchControl = new FormControl('mobile');
    this.placeholderValue = 'Search Patient by Mobile Number';
    this.searchType = 'mobile';
    this.patinetSearchControl.valueChanges.subscribe(value => {
      this.searchType = value;
      this.patientData = null;
      switch (this.searchType) {
        case 'name':
          this.placeholderValue = 'Search Patient by Name';
          break;
        case 'aadhaar':
          this.placeholderValue = 'Search Patient by Aadhaar Number';
          break;
        case 'patientId':
          this.placeholderValue = 'Search Patient by Hospital ID';
          break;
        default:
          this.placeholderValue = 'Search Patient by Mobile Number';
          break;
      }
    });
  }
  checkValidations() {
    this.isEnable = false;

    if (this.searchType === 'name') {
      if (this.patientData && this.patientData.length > 2 ) {
        this.isEnable = true;

      }
    }
    if (this.searchType === 'aadhaar') {
      if (this.patientData && this.patientData.length === 12) {
        this.isEnable = true;

      }

    }
    if (this.searchType === 'patientId') {
      if (this.patientData && this.patientData.length > 6 ) {
        this.isEnable = true;

      }

    }
    if (this.searchType === 'mobile') {
      if (this.patientData && this.patientData.length === 10 ) {
        this.isEnable = true;

      }
    }

  }
  totalCount() {
    this.router.navigate(['all-patients'], {
      relativeTo: this.activatedRoute
    });
  }
  addPatient() {
    this.router.navigate(['add-patient'], {
      relativeTo: this.activatedRoute
    });
  }
  quickRegister() {
    const navigationExtras: NavigationExtras = {
      queryParams: {
        type: 'quick'
      }
    };
    this.router.navigate(['/registration/add-patient'], navigationExtras);
  }

/**
 * @description this method is used for search the patient based on condition.
 * @function searchPatient
 * @param patientData
 * @param searchType
 * @param data is optional
 */
  searchPatient(searchType, patientData, data?) {
    this.patientDataService.setUserVisitIdData(null);
    this.checkValidations();
    if (!this.isEnable) {
     return false;
    }

    // this code is checking for search with finger or not
    if (data) {
      const dialog = this.dialog.open(FingerPrintComponent, {
        width: '350px',
        data: {
          data: '',
          isSearch: true
        }
      });
      dialog.afterClosed().subscribe(res => {
        if (res) {
          this.opdService.fingerData.next(res);
          this.proceedSearch(searchType, patientData);
        }
      });
    } else {
      this.opdService.fingerData.next(null);
      this.proceedSearch(searchType, patientData);
    }
  }

/**
 * @description this method is used for proceeding search and navigate to search patint screen.
 * @function proceedSearch
 * @param searchType
 * @param patientData
 */
  proceedSearch(searchType, patientData) {
    this.i = ++ this.i;
    const navigationExtras: NavigationExtras = {
      queryParams: {
        patientData,
        searchType,
        i: this.i
      }
    };
    this.isEnable = false;
    this.billingserviceService.setConsultationFormData([]);
    this.billingserviceService.setConsultations([]);
    this.billingserviceService.setLabFormData([]);
    this.billingserviceService.setLabTests([]);
    this.billingserviceService.setLabPanels([]);
    this.billingserviceService.setLabPackages([]);
    this.billingserviceService.setImageFormdata([]);
    this.billingserviceService.setImageTests([]);
    this.billingserviceService.setImagePanels([]);
    this.billingserviceService.setImagePackages([]);
    this.billingserviceService.setEmrTests(null);
    this.billingserviceService.setEmrImagingTests(null);
    this.patientData = null;
    this.router.navigate(['/registration/search-patient'], navigationExtras);
  }
}
